export default class OldCalculator {
  constructor() {
    this.operations = function(term1, term2, operation) {
      switch (operation) {
        case "add":
          return { res: term1 + term2 };
        case "sub":
          return { res: term1 - term2 };
        default:
          return NaN;
      }
    };
  }
}
